import DemandeAppuiService from "src/modules/demandeAppui/demandeAppuiService";
import selectors from "src/modules/demandeAppui/list/demandeAppuiListSelectors";
import { i18n } from "src/i18n";
import Errors from "src/modules/shared/error/errors";

const prefix = "DEMANDEAPPUI_LIST";

const demandeAppuiListActions = {
  FETCH_STARTED: `${prefix}_FETCH_STARTED`,
  FETCH_SUCCESS: `${prefix}_FETCH_SUCCESS`,
  FETCH_ERROR: `${prefix}_FETCH_ERROR`,

  RESETED: `${prefix}_RESETED`,
  SELECTEDS_CHANGED: `${prefix}_SELECTEDS_CHANGED`,

  PAGINATION_CHANGED: `${prefix}_PAGINATION_CHANGED`,
  SORTER_CHANGED: `${prefix}_SORTER_CHANGED`,

  EXPORT_STARTED: `${prefix}_EXPORT_STARTED`,
  EXPORT_SUCCESS: `${prefix}_EXPORT_SUCCESS`,
  EXPORT_ERROR: `${prefix}_EXPORT_ERROR`,

  doChangeSelected(payload) {
    return {
      type: demandeAppuiListActions.SELECTEDS_CHANGED,
      payload,
    };
  },

  doReset: () => async (dispatch) => {
    dispatch({
      type: demandeAppuiListActions.RESETED,
    });

    dispatch(demandeAppuiListActions.doFetch());
  },

  doChangePaginationAndSort:
    (pagination, sorter) => async (dispatch, getState) => {
      dispatch({
        type: demandeAppuiListActions.PAGINATION_CHANGED,
        payload: pagination,
      });

      dispatch({
        type: demandeAppuiListActions.SORTER_CHANGED,
        payload: sorter,
      });

      dispatch(demandeAppuiListActions.doFetchCurrentFilter());
    },

  doFetchCurrentFilter: () => async (dispatch, getState) => {
    const filter = selectors.selectFilter(getState());
    const rawFilter = selectors.selectRawFilter(getState());
    dispatch(demandeAppuiListActions.doFetch(filter, rawFilter, true));
  },

  doFetch:
    (filter?, rawFilter?, keepPagination = false) =>
    async (dispatch, getState) => {
      try {
        dispatch({
          type: demandeAppuiListActions.FETCH_STARTED,
          payload: { filter, rawFilter, keepPagination },
        });

        const response = await DemandeAppuiService.list(
          filter,
          selectors.selectOrderBy(getState()),
          selectors.selectLimit(getState()),
          selectors.selectOffset(getState())
        );

        dispatch({
          type: demandeAppuiListActions.FETCH_SUCCESS,
          payload: {
            rows: response.rows,
            count: response.count,
          },
        });
      } catch (error) {
        Errors.handle(error);

        dispatch({
          type: demandeAppuiListActions.FETCH_ERROR,
        });
      }
    },

  doSearch:
    (filter?, rawFilter?, keepPagination = false) =>
    async (dispatch, getState) => {
      try {
        dispatch({
          type: demandeAppuiListActions.FETCH_STARTED,
          payload: { filter, rawFilter, keepPagination },
        });

        const response = await DemandeAppuiService.search(
          filter,
          selectors.selectOrderBy(getState()),
          selectors.selectLimit(getState()),
          selectors.selectOffset(getState())
        );

        dispatch({
          type: demandeAppuiListActions.FETCH_SUCCESS,
          payload: {
            rows: response.rows,
            count: response.count,
          },
        });
      } catch (error) {
        Errors.handle(error);

        dispatch({
          type: demandeAppuiListActions.FETCH_ERROR,
        });
      }
    },
};

export default demandeAppuiListActions;
